import { ref } from 'vue';
export default {
  setup() {
    const YxsOption = ref("");
    const sysDialogVisible = ref(false);

    const handleClose = done => {
      ElMessageBox.confirm('确定关闭对话框?', '温馨提示', {
        type: 'info',
        center: true
      }).then(() => {
        done();
      });
    };

    return {
      YxsOption,
      handleClose,
      sysDialogVisible
    };
  },

  data() {
    return {
      // 分页
      currentPage: 1,
      pagesize: 10,
      // 搜索框
      search: "",
      // 院系所信息
      YxsList: [],
      Yxs: {},
      // 实验室列表
      SysList: [],
      Sys: {},
      activeName: "first",
      // 设备
      SbList: [],
      Sb: {}
    };
  },

  created() {
    this.getYxsList();
  },

  methods: {
    // 分页
    handleSizeChange: function (size) {
      this.pagesize = size;
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
    },

    // 选择院系所
    changeYxs() {
      this.Yxs = JSON.parse(JSON.stringify(this.YxsOption));
      this.YxsOption = this.Yxs.dwmc;
      Loading.show();
      this.getSysListById(this.Yxs.dwh);
      Loading.hide();
    },

    // 获取所有院系所信息
    getYxsList() {
      this.$axios.get("/weixin-yxs/getAllYxs").then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.YxsList = data.data;
        }
      });
    },

    // 根据id获取实验室列表
    getSysListById(id) {
      this.$axios.post("/weixin-syszk/yxsFaultSysList", {
        id: id
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.SysList = data.data;
        }
      });
    },

    // 根据实验室id 获取 需要维修设备的列表
    getSbFaultList(id) {
      this.$axios.post("/weixin-sbzk/sbFaultSbList", {
        sysh: id
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.SbList = data.data;
        }
      });
    },

    // 打开实验室维修管理的对话框
    repairSysDialog(vaule) {
      this.Sys = JSON.parse(JSON.stringify(vaule));
      this.sysDialogVisible = true;
      Loading.show();
      this.getSbFaultList(this.Sys.sysh);
      Loading.hide();
    },

    // 选择 维修设备类型
    handleClick(value) {
      if (value.props.name == "second") {
        // 获取 设备
        if (this.SbList.length == 0) {
          Loading.show();
          this.getSbFaultList(this.Sys.sysh);
          Loading.hide();
        }
      }
    },

    // 维修实验室
    sysRepairButton() {
      Loading.show();
      this.$axios.post("/weixin-syszk/repairSys", {
        sysh: this.Sys.sysh,
        info: "",
        systype: "0"
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          ElMessage.success("维修成功");
          Loading.hide(); // 刷新实验室列表

          this.sysDialogVisible = false;
          Loading.show();
          this.getSysListById(this.Yxs.dwh);
          Loading.hide();
        }
      }).catch(error => {
        Loading.hide();
      });
    },

    // 维修实验室的设备
    sbRepairButton(value) {
      Loading.show();
      this.$axios.post("/weixin-sbzk/repairSb", {
        sysh: this.Sys.sysh,
        info: "",
        sbzt: "0",
        sbh: value
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          ElMessage.success("维修成功");
          Loading.hide();
          this.sysDialogVisible = false;
          Loading.show();
          this.getSysListById(this.Yxs.dwh);
          Loading.hide();
        }
      }).catch(error => {
        Loading.hide();
      });
    }

  },
  computed: {
    getSysFilter() {
      return this.SysList.map(t => {
        if (t.systype == 0) {
          t['state'] = "开放";
        } else if (t.systype == 1) {
          t['state'] = "禁用";
        } else {
          t['state'] = "报修";
        }

        return t;
      }).filter(data => !this.search || data.sysmc.includes(this.search.toLowerCase()) || data.sysmph.includes(this.search.toLowerCase()) || data.state.includes(this.search.toLowerCase()));
    },

    sbTableInfo() {
      return this.SbList.map(t => {
        if (t.sbzt == 0) {
          t['state'] = "开放";
        } else if (t.sbzt == 1) {
          t['state'] = "禁用";
        } else {
          t['state'] = "报修";
        }

        return t;
      });
    }

  }
};